@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Arial.woff2") format("woff2"), url("../fonts/Arial.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button-show-more {
  background-color: #f2f2f2;
  border: none;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  color: #444444;
}

.button-program {
  background-color: #68b738;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  text-decoration: none;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
}

.button-order {
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  color: #222222;
  background-color: #68b738;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  text-decoration: none;
}

.page {
